.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.monitor .poplar-map-card,
.monitor .monitor-list-card {
  margin-bottom: 24px;
}
.monitor #list-bar {
  height: 490px;
  width: 100%;
}
.monitor #trading-proportion,
.monitor #resource-surplus,
.monitor #gaugeChart {
  height: 200px;
}
.monitor #resource-surplus {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  border: 2px solid var(--primary);
  background: var(--primary-lighten);
  position: relative;
  overflow: hidden;
}
.monitor #resource-surplus .wave {
  content: '';
  position: absolute;
  height: 400px;
  width: 400px;
  top: -25%;
  left: 50%;
  margin-left: -200px;
  margin-top: -200px;
  border-radius: 35%;
  background: white;
  -webkit-animation: wave 15s infinite linear;
          animation: wave 15s infinite linear;
}
.monitor #resource-surplus p {
  color: var(--primary-lightener);
  font-size: 30px;
  margin: 0;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
}
@-webkit-keyframes wave {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes wave {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
